"use client";
import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { useSearchParams, useRouter } from "next/navigation";
import { getImageUrl, TV } from "../../lib/getImageUrl";
import { LiquidButton } from "@/components/ui/liquid-glass-button";

const TMDB_API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY || "1070730380f5fee0d87cf0382670b255";
const TMDB_BASE = "https://api.themoviedb.org/3";

export default function TVPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const page = parseInt(searchParams.get("page") || "1", 10);
  const [shows, setShows] = useState<TV[]>([]);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    fetch(`${TMDB_BASE}/tv/popular?api_key=${TMDB_API_KEY}&page=${page}`)
      .then(res => res.json())
      .then(data => {
        setShows(data.results || []);
        setTotalPages(Math.min(data.total_pages, 500)); // TMDB max 500
        setLoading(false);
      });
  }, [page]);

  const goToPage = (p: number) => {
    router.push(`/tv?page=${p}`);
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-extrabold text-white mb-8 drop-shadow-lg">Popular TV Shows</h1>
      {loading ? (
        <div className="text-white text-lg">Loading...</div>
      ) : (
        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-6">
          {shows.map((show) => (
            <Link key={show.id} href={`/watch/tv/${show.id}`} className="group">
              <div className="glass-card rounded-2xl overflow-hidden shadow-xl transition-all duration-200">
                <div className="relative w-full aspect-[2/3] bg-black/30">
                  <Image
                    src={getImageUrl(show)}
                    alt={show.name || ''}
                    fill
                    sizes="200px"
                    className="object-cover w-full h-full group-hover:scale-105 transition-transform duration-200"
                  />
                </div>
                <div className="p-3">
                  <div className="font-bold text-base text-white truncate" title={show.name}>{show.name}</div>
                  <div className="text-white/80 text-xs mt-1 line-clamp-2">{show.overview}</div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      )}
      {/* Pagination */}
      <div className="flex justify-center items-center gap-4 mt-10">
        <LiquidButton
          variant="default"
          size="icon"
          className="rounded-full"
          onClick={() => goToPage(page - 1)}
          disabled={page <= 1}
          aria-label="Previous page"
        >
          <span className="text-2xl">&#8592;</span>
        </LiquidButton>
        <span className="text-white font-bold">Page {page}</span>
        <LiquidButton
          variant="default"
          size="icon"
          className="rounded-full"
          onClick={() => goToPage(page + 1)}
          aria-label="Next page"
        >
          <span className="text-2xl">&#8594;</span>
        </LiquidButton>
      </div>
    </div>
  );
} 